	module.exports = {"data":{"post":{"html":"<p>http://www.hislifeasaboy.com has a couple of new pictures</p>\n<p>Bier Verbot! The city has banned the Hamburg SV soccer club from selling strong beer at the stadium.... just before their match with league-leading Bayern Munich on Sunday. </p>\n<p><a href=\"http://www.internetworld.de/index_1592.html\">\"Webspace\" Trademark Revoked</a> (in German). German trademark owners who are not actively using their trademark can no longer claim damages from unsuspecting web sites for simply using the term. Even Microsoft was forced to pay for \"Explorer\".</p>\n<p>Our stock is up to 30 today... another 30% jump.</p>\n<p>I've been updating the list of <a href=\"http://shanson.editthispage.com/stories/storyReader$125\">German EditThisPage / Manila Sites</a> as I discover new sites.</p>","fields":{"title":"Wednesday","date":"02 Feb 2000","url":"https://papascott.de/2000-02-02-wednesday/"}}},"pathContext":{"slug":"/2000-02-02-wednesday/","prev":{"fields":{"title":"Inspired by the Readers","date":"01 Feb 2000","slug":"/2000-02-01-inspired-by-the-readers/"}},"next":{"fields":{"title":"Thursday","date":"03 Feb 2000","slug":"/2000-02-03-thursday/"}}}}

